
import {LoadMore,Popup,Lazyload, Actionsheet,Toast,MessageBox} from "mint-ui"
import iHeader from "@/components/header/header-r"
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'








export default {
name: "credit_circle",
data() {
return {
users:localStorage.getItem('users')?JSON.parse(localStorage.getItem('users')):'',
more_state:1,
left:'',
leftonoff:false,
dianzan:[],
page: 1,
activeinputall:false,
reloadState:1,
loading:false,
popupVisible:false,
title: "交流圈",
arrow:true,
list:[],
showcommon:false,
activezan:false,
textmodel:'',
listcomdetail:[],
id:'',
pid:'',
idshow:'',
comlist:[],
comlistshow:false,
indexed:'',
indexn:'',
touser:'',
showhuifu:false,
nickname:'',
click_num_id:'',
clientHeight:window.innerHeight,   // 这里是给到了一个默认值 （这个很重要）
timer:false,
isFocus:false,
read:true,
defaultImg: 'this.src="' + require('@/assets/images/head.png') + '"',
show5:false
};
},
components: {
iHeader,LoadMore,Popup,Lazyload,Actionsheet,ConfirmPlugin,Confirm
},
created() {
this.getList();






},
mounted(){

},
methods:{
getList(page = 1,callback,reload){

























this.$store.dispatch("get_list", {
data: "data="+JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
page:page}),
fn: (code, msg) => {
switch (code) {
case 200:
callback&&callback();
if(reload){
this.list = msg.list;
this.reloadState = 1;
return false;
}
if(msg.list.length<=msg.pageSize){
this.more_state = 2;
this.loading = true;
}
this.list = [...this.list,...msg.list];
break;
case -1:
this.reloadState = 2;
this.$router.push('/my/login');
break;
default:
Toast("请求出错")
break;
}
}
})
},
loadMore(){
this.page++;
this.loading = true;
this.getList(this.page,()=>{
this.loading = false;
});
},
loadTop(){
this.page = 1;
this.getList(this.page,()=>{
this.popupVisible = true;
this.$refs.loadmore.onTopLoaded();
this.more_state = 1;
this.loadMore();
setTimeout(()=>{
this.popupVisible = false;
},2000);
},true)
},

addcomment(id,index){
var _this=this;
this.leftonoff=false;
this.comlistshow=true;
this.id=id;
this.indexed=index;

setTimeout(() => {

_this.show5=true;
}, 500);
},
onConfirm5 (value) {


this.buttonAddTodo();
},
onShow5 () {

},
onConfirm (msg) {
if (msg) {
alert(msg)
}
},
onCancel () {
console.log('on cancel')
},






















addcommentinside(id,n,tousers,nicknameusers,comment,index){
this.id=id;
this.pid=1;
this.indexn=n;
this.touser=tousers;
this.nickname=nicknameusers;
if(comment==1){
var _this=this;
MessageBox.confirm('确定删除该评论?').then(action => {
this.$store.dispatch("del_list", {
data: "data="+JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
id:this.touser,
}),
fn: (code, msg) => {

switch (code) {
case 200:
Toast(msg);
_this.touser='';
console.log(_this.list[n].comment_num,n)
--_this.list[index].comment_num;
if(_this.list[index].comment_num<=0){
_this.list[index].comment_num=0;
}
_this.common_list(id,n);
break;
case -1:
Toast(msg)
break;
default:
Toast("请求出错")
break;
}
}
})
});
return false;
}else {
var _this=this;
setTimeout(function(){


_this. comment();
},300);
return false;
}
},

zan(id,index){
this.$axios.post('/api/Post/do_likenum',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
post_id:id
})}`).then((res)=>{
if(res.code==200){

this.leftonoff=false;

if(this.list[index].is_likenum===0){
this.list[index].is_likenum=1;
this.dianzan[index]=false;


++this.list[index].click_num;
}else{
this.list[index].is_likenum=0;
this.dianzan[index]=true;


--this.list[index].click_num;
}
}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
},

leftid(id,index){
this.idshow=id;
this.left=index;
this.common_list(id,index);

},

buttonAddTodo(){

if(!this.textmodel){
Toast('请输入内容');
return false;
};
if(this.pid!=1){
this.$axios.post('/api/Post/do_comment',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
post_id:this.id,
content:this.textmodel,
})}`).then((res)=>{
if(res.code==200){
var num=this.indexed;
num=Number(num)||0;

this.comlist.push({nickname:this.users.nickname,content:this.textmodel,post_id:this.id});
++this.list[num].comment_num;
Toast(res.msg);
this.getList();

this.textmodel='';


}else{

this.textmodel='';


Toast(res.msg)
}
}).catch((err)=>{throw err});
return false;
}else  if(this.pid==1){
this.$axios.post('/api/Post/do_comment',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
post_id:this.id,
content:this.textmodel,
pid:this.touser,
})}`).then((res)=>{
if(res.code==200){
var num=this.indexn;
num=Number(num)||0;
this.comlist.push({nickname:this.users.nickname,content:this.textmodel,post_id:this.id,to_user:this.nickname});
Toast(res.msg);
this.activeinputall=false;
this.textmodel='';


this.touser='';
this.getList();
}else{
this.activeinputall=false;
this.textmodel='';


Toast(res.msg)
}
}).catch((err)=>{throw err});
return false;
}
},

common_list(id,index){

if(this.comlistshow==false)
{
localStorage.setItem('idshow',this.idshow);
this.comlistshow=true;
this.leftonoff=true;
this.click_num_id=id;
this.$axios.post('/api/Post/v1_see_comment',
`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
id:id,
})}`).then((res)=>{
if(res.code==200){
this.comlist=res.data.list.comment;

}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
return false;
}else if(this.comlistshow==true)
{
var getidshow= parseInt(localStorage.getItem('idshow'));

if(id===getidshow){//对比是否是点击的相同的输入图标
this.comlistshow=false;
this.leftonoff=false;
this.click_num_id=id;
return false;
}else{
this.comlistshow=true;
this.leftonoff=true;
this.click_num_id=id;
this.$axios.post('/api/Post/v1_see_comment',
`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
id:id,
})}`).then((res)=>{
if(res.code==200){
this.comlist=res.data.list.comment;

}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
return false;
}
}
},
delpost(id,index){
MessageBox.confirm('确定删除该帖子?').then(action => {
this.$axios.post('/api/Post/del_my_post',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
id:id,
})}`).then((res)=>{
if(res.code==200){
Toast(res.msg);
this.list.splice(index,1);
}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
});
},
calc:function(){
document.getElementById('lines').value = document.getElementById('text').value.split("\n").length;
},
offFocus:function(){
this.activeinputall=false;
},
},
watch:{












/*  'clientHeight':function(n,o) {
if (n<o) {
setTimeout(function(){
this.$refs.inputall.scrollIntoView(true);
},100);
}else{

}
},
'activeinputall':function(n,o){
if(n===true){
$('.mint-tabbar').css('z-index','-1');
$('.main').css('bottom','0');
}else{
$('.mint-tabbar').css('z-index','1');
$('.main').css('bottom','60px');
}
},  */
dianzanindex(n,o){
console.log(n,o)
}
},
computed:{
dianzanindex(){
return function(){
var zanindex=localStorage.getItem('zanindex');
var dianzan=localStorage.getItem('dianzan');
if(localStorage.getItem('dianzan')=='true'){
return    this.dianzan[zanindex]=true;
}else{
return  this.dianzan[zanindex]=false;
}
}
}
},
directives: {
focus: {
update:function(el){//组件更新
el.focus();
},
}
},
}



